@use "./top-mv";
@use "../../base/base" as b;
@use "../../base/mixin" as m;

// arrow-right-btn
.arrow-right-btn {
  display: inline-block;
  text-align: center;
  padding: 2rem 1rem;
  color: #3e3a39;
  font-size: 1.6rem;
  line-height: 1.25;
  font-family: "Playfair Display", serif;
  letter-spacing: 0.2em;
  width: 28rem;
  max-width: 100%;
  border-radius: 30px;
  border: 1px solid currentColor;
  transition: 0.5s ease;
  position: relative;
  &::after {
    content: "";
    display: inline-block;
    position: absolute;
    top: 0;
    right: -3rem;
    bottom: 0;
    margin: auto;
    width: 7rem;
    height: 1rem;
    border-right: 1px solid Currentcolor;
    border-bottom: 1px solid Currentcolor;
    transform: skew(45deg) translateY(-20%);
    transition: 0.7s ease;
  }
  &:focus,
  &:hover {
    color: #fff;
    background: rgba(62, 58, 57, 0.4);
    border-color: rgba(62, 58, 57, 0.4);
    &::after {
      transform: skew(45deg) translate(4rem, -20%);
      opacity: 0;
    }
  }
}

// lv1-heading
.lv1-heading {
  &__title {
    color: #fff;
    font-size: clamp(4.5rem, 4.0625vw, 8.5rem);
    font-family: "Playfair Display", serif;
    font-weight: bold;
    letter-spacing: 0.2em;
  }
  &__text {
    color: #fff;
    font-size: clamp(1.6rem, 1.25vw, 4rem);
    margin-top: 1.5rem;
    text-align: center;
    @media screen and (min-width: 600px) {
      margin-top: 2rem;
    }
  }
}

// low-lv1-heading
.low-lv1-heading {
  font-size: 3rem;
  font-weight: bold;
  font-family: "Playfair Display", serif;
  letter-spacing: 0.1em;
  @media screen and (min-width: 600px) {
    font-size: 3.5rem;
  }
}

// lv2-heading
.lv2-heading {
  &__title {
    font-size: 3rem;
    font-weight: bold;
    font-family: "Playfair Display", serif;
    letter-spacing: 0.1em;
    @media screen and (min-width: 600px) {
      font-size: 3.5rem;
    }
  }
  &__title--border-right {
    text-align: right;
    position: relative;
    &::before {
      content: "";
      position: absolute;
      display: block;
      top: 50%;
      right: calc(((100vw - 108rem) / 2 + 20px + 2rem) * -1);
      height: 1px;
      width: calc((100vw - 108rem) / 2 + 20px + 2rem - 1vw);
      transform: translateY(-50%);
      background: currentColor;
    }
  }
  &__title--border-left {
    position: relative;
    &::before {
      content: "";
      position: absolute;
      display: block;
      top: 50%;
      left: calc(((100vw - 108rem) / 2 + 20px + 2rem) * -1);
      height: 1px;
      width: calc((100vw - 108rem) / 2 + 20px + 2rem - 1vw);
      transform: translateY(-50%);
      background: currentColor;
    }
  }
  &__text {
    margin-top: 2rem;
    font-size: 1.8rem;
    line-height: 1.5;
    @media screen and (min-width: 600px) {
      font-size: 2rem;
    }
  }
}

// lv3-heading
.lv3-heading {
  &__title {
    font-size: 1.8rem;
    font-weight: bold;
    line-height: 1.5;
    @media screen and (min-width: 600px) {
      font-size: 3.2rem;
      line-height: 1.2;
    }
  }
}

// container
.container {
  max-width: 108rem;
  margin-right: auto;
  margin-left: auto;
  padding-right: 2rem;
  padding-left: 2rem;
}

//  low-container
.low-container {
  max-width: 90rem;
  margin-right: auto;
  margin-left: auto;
  padding-right: 2rem;
  padding-left: 2rem;
}

.lead {
  padding-top: 7rem;
  &__title-text {
    text-align: center;
    margin-top: 3rem;
  }
}

.top-faq {
  margin-top: 6rem;
}

.faq {
  &__panel {
    display: block;
    width: 100%;
    padding: 0;
    border: none;
    background: none;
    border-radius: 2.5rem;
    &:first-of-type {
      .faq__main {
        display: block;
      }
    }
    &:nth-of-type(n + 2) {
      margin-top: 2.3rem;
    }
  }
  &__header {
    position: relative;
    padding: 1rem 1.05rem 0.6rem;
    background: linear-gradient(to right, #599ae0, #bbebdc);
    box-shadow: 0 3px 6px rgba(0, 0, 0, 0.16);
    border-radius: 1.3rem;
    transition: 0.3s ease;
    @media screen and (min-width: 768px) {
      padding: 2.6rem 3rem;
      border-radius: 2.5rem;
    }
  }
  &__question {
    position: relative;
    text-align: left;
    display: flex;
    flex-direction: column;
    padding-right: 3.5rem;
    @media screen and (min-width: 768px) {
      display: block;
      padding-right: 6rem;
      padding-left: 6rem;
    }
    &::before,
    &::after {
      content: "";
      position: absolute;
      z-index: 5;
      top: 50%;
      right: 0;
      display: inline-block;
      height: 0.25rem;
      width: 1.8rem;
      background: #fff;
      border-radius: 1.75rem;
      transition: transform 0.3s ease;
      transform: translateY(-50%);
      @media screen and (min-width: 768px) {
        height: 0.45rem;
        width: 3rem;
        border-radius: 2rem;
      }
      @media screen and (min-width: 1024px) {
        height: 0.4rem;
        width: 3rem;
        border-radius: initial;
      }
    }
    &::after {
      transform: translateY(-50%) rotate(90deg);
    }
  }
  &__header.is-open {
    border-radius: 1.3rem 1.3rem 0 0;
    @media screen and (min-width: 768px) {
      border-radius: 2.5rem 2.5rem 0 0;
      box-shadow: initial;
    }
    .faq__question {
      &::after {
        transform: translateY(-50%) rotate(0);
      }
    }
  }
  &__header.is-open + &__main {
    box-shadow: 0 3px 6px rgba(0, 0, 0, 0.16);
  }
  &__icon {
    display: inline-block;
    color: #fff;
    font-size: 1.5rem;
    font-weight: 700;
    font-family: "Playfair Display", serif;
    @media screen and (min-width: 768px) {
      position: absolute;
      left: 0;
      font-size: 2.8rem;
    }
    @media screen and (min-width: 1024px) {
      font-size: 3rem;
    }
  }
  &__icon--question {
    @media screen and (min-width: 768px) {
      top: 50%;
      transform: translateY(-50%);
    }
  }
  &__icon--answer {
    color: #3e3a39;
    @media screen and (min-width: 768px) {
      top: 0.3rem;
    }
  }
  &__title {
    margin-top: 0.8rem;
    color: #fff;
    font-size: 1.4rem;
    font-weight: 700;
    line-height: 1.6;
    letter-spacing: 0.01em;
    @media screen and (min-width: 768px) {
      margin-top: 0;
      font-size: 1.8rem;
      line-height: 1.42222;
    }
  }
  &__main {
    position: relative;
    display: none;
    padding: 0 1.05rem;
    background: #fff;
    border-radius: 0 0 1.3rem 1.3rem;
    @media screen and (min-width: 768px) {
      padding: 2.5rem 3rem;
      border-radius: 0 0 2.5rem 2.5rem;
    }
  }
  &__answer {
    text-align: left;
    @media screen and (min-width: 768px) {
      position: relative;
      padding-left: 6rem;
    }
  }
  &__contents {
    margin-top: 0.8rem;
    text-align: left;
    @media screen and (min-width: 768px) {
      margin-top: 0;
    }
  }
  &__text {
    color: #3e3a39;
    font-size: 1.4rem;
    line-height: 1.6;
    @media screen and (min-width: 768px) {
      font-size: 1.6rem;
    }
  }
}
